import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

export default class extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            users: [
                {
                    "_id": "5b55b18c50b5822f9079f0d4",
                    "userName": "wildan",
                    "password": "wildan123",
                    "name": {
                        "first": "M",
                        "mid": "wildan",
                        "last": "B"
                    },
                    "email": "wildan@xsis.co.id",
                    "phone": "12345689",
                    "active": 1,
                    "createDate": "2018-07-23T10:44:28.650Z",
                    "modifyDate": "2018-07-23T10:44:28.650Z"
                },
                {
                    "_id": "5b55b18c50b5822f9079f0d5",
                    "userName": "Udin",
                    "password": "udin123",
                    "name": {
                        "first": "Mark",
                        "mid": "Udin",
                        "last": "Tiler"
                    },
                    "email": "udin@xsis.co.id",
                    "phone": "0088877711",
                    "active": 1,
                    "createDate": "2018-07-23T10:44:28.650Z",
                    "modifyDate": "2018-07-23T10:44:28.650Z"
                }
            ]
        }
    }
    render() {
        const users = this.state.users;

        return (
            <div>
                <h3>List of Users</h3>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>User Name</TableCell>
                            <TableCell>Name</TableCell>
                            <TableCell>Email</TableCell>
                            <TableCell>Phone</TableCell>
                            <TableCell>Active</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {users.map(n => {
                            return (
                                <TableRow key={n._id}>
                                    <TableCell component="th" scope="row">
                                        {n.userName}
                                    </TableCell>
                                    <TableCell >{n.name.first}</TableCell>
                                    <TableCell >{n.email}</TableCell>
                                    <TableCell >{n.phone}</TableCell>
                                    <TableCell >{n.active}</TableCell>
                                </TableRow>
                            );
                        })}
                    </TableBody>
                </Table>
            </div>
        )
    }
}


